@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
body {
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a:link,
a:visited,
a:hover,
a:focus {
  color: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  padding: 0 20px;
}

@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    max-width: 1220px;
    margin: 0 auto;
    padding: 0;
  }
}
.header {
  padding-top: 14px;
}

.header__nav {
  width: 100%;
}

@media (min-width: 768px) {
  .header {
    display: flex;
    padding: 25px 45px;
    justify-content: space-between;
  }

  .header.container {
    padding: 20px;
  }
}
@media (min-width: 1440px) {
  .header.container {
    padding: 0;
  }
}
.green-link {
  display: block;
  background-color: #68b738;
  text-transform: uppercase;
  color: #ffffff;
  width: auto;
  text-align: center;
  padding: 1em 0;
  text-decoration: none;
  font-size: 14px;
  margin: 0 20px;
}

.button {
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  display: block;
  background-color: #68b738;
  text-transform: uppercase;
  color: #ffffff;
  width: 100%;
  text-align: center;
  padding: 10px 0;
  text-decoration: none;
  border: none;
  transition: 0.2s;
}
.button:hover {
  cursor: pointer;
  background-color: #5eaa2f;
  transition: 0.2s;
}
.button:active {
  color: rgba(255, 255, 255, 0.7);
}

@media (min-width: 768px) and (min-width: 1440px) {
  .button {
    font-size: 20px;
  }
}
.top-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
}

@media (min-width: 768px) {
  .top-menu {
    z-index: 200;
  }
}
@media (min-width: 1440px) {
  .top-menu {
    position: absolute;
  }
}
.logo__link {
  display: flex;
  justify-content: space-between;
}

.logo__image {
  display: block;
  margin-right: 57px;
}

@media (min-width: 768px) {
  .logo {
    margin-left: 4px;
    margin-top: 5px;
  }

  .logo__image {
    margin-left: 8px;
    margin-right: 0;
    transition: 0.2s;
  }
  .logo__image:hover {
    opacity: 0.8;
  }
  .logo__image:active {
    opacity: 0.6;
  }
}
@media (min-width: 1440px) {
  .logo__link {
    width: 220px;
    position: absolute;
    top: 55px;
    left: -7px;
    justify-content: initial;
  }

  .logo__image--sign {
    width: 120px;
    margin-left: 11px;
  }
}
.burger-menu-button {
  border: none;
  background-color: transparent;
  width: 35px;
  height: 35px;
  position: relative;
  padding: 0 4px;
}
.burger-menu-button:hover {
  cursor: pointer;
}

.burger-menu-button .burger-inner {
  height: 1.5px;
  background-color: #000000;
  position: relative;
  display: block;
  width: 23px;
}
.burger-menu-button .burger-inner::before, .burger-menu-button .burger-inner::after {
  content: "";
  height: 1.5px;
  width: 23px;
  position: absolute;
  background-color: #000000;
  left: 0;
}
.burger-menu-button .burger-inner::before {
  transform: translateY(8px);
  transition: 0.1s;
}
.burger-menu-button .burger-inner::after {
  transform: translateY(-8px);
  transition: 0.1s;
}

.burger-menu-button--open {
  transition: 0.3s;
}
.burger-menu-button--open .burger-inner {
  height: 1.5px;
  background-color: transparent;
  position: relative;
}
.burger-menu-button--open .burger-inner::before, .burger-menu-button--open .burger-inner::after {
  content: "";
  height: 2px;
  width: 23px;
  position: absolute;
  background-color: #000000;
  left: 0;
  transition: 0.2s;
}
.burger-menu-button--open .burger-inner::before {
  transform: rotate(45deg);
}
.burger-menu-button--open .burger-inner::after {
  transform: rotate(-45deg);
}

@media (min-width: 768px) {
  .burger-menu-button {
    display: none;
  }
}
.main-nav {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  padding: 0;
  width: 100%;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  margin-top: 10px;
  margin-bottom: 0;
  background-color: #ffffff;
  z-index: 900;
}
.main-nav--closed {
  position: absolute;
  transform: translateY(-500px);
}
.main-nav--float {
  position: absolute;
  margin: 0;
  left: 0;
  transition: 0.3s ease-in-out;
}

.main-nav__element {
  text-transform: uppercase;
  padding: 1em 0;
  width: 100%;
  border-top: 1px solid #ebebeb;
  text-align: center;
}
.main-nav__element:last-child {
  border-bottom: 1px solid #ebebeb;
}

@media (min-width: 768px) {
  .main-nav {
    display: flex;
    flex-direction: row;
    padding: 0;
    margin: 0;
    max-width: 614px;
    justify-content: flex-end;
    font-size: 18px;
    font-weight: 400;
    flex-wrap: wrap;
    z-index: 100;
  }
  .main-nav--closed {
    position: inherit;
  }
  .main-nav--float {
    position: inherit;
  }

  .main-nav__element {
    font-size: 18px;
    width: auto;
    padding-left: 7px;
    padding-right: 10px;
    margin-right: 19px;
    border: none;
  }
  .main-nav__element:last-child {
    border: none;
    margin: 0;
  }

  .main-nav__link {
    text-decoration: none;
    color: inherit;
  }
  .main-nav__link--active {
    border-bottom: 2px solid #68b738;
    padding-bottom: 5px;
  }
  .main-nav__link:visited {
    color: #000000;
  }

  .main-nav__element .main-nav__link {
    color: #000000;
    border-color: #000000;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    position: absolute;
    background-color: transparent;
    top: 50px;
    left: 733px;
  }

  .main-nav__element {
    font-size: 20px;
  }
  .main-nav__element:last-child {
    border: none;
  }

  .main-nav__link {
    color: #ffffff;
    border-color: #ffffff;
    transition: 0.2s;
  }
  .main-nav__link--active {
    border-color: #68b738;
  }
  .main-nav__link:hover {
    opacity: 0.6;
    transition: 0.2s;
  }
  .main-nav__link:active {
    opacity: 0.3;
    transition: 0.2s;
  }
}
@media (min-width: 1441px) {
  .main-nav {
    right: 15%;
  }
}
@media (min-width: 1440px) {
  .main-nav--contrast {
    color: #ffffff;
    border-color: #ffffff;
  }
  .main-nav--contrast .main-nav__link--active {
    border-color: #ffffff;
  }
}
