}

.example__wrapper {
  display: flex;
  align-items: center;
  width: 200%;
}

.example--after .example__wrapper,
.example--after .example__progress-toggle {
  transform: translateX(-50%);
  transition: 0.3s;
}

.example .example__wrapper,
.example .example__progress-toggle {
