  .request-form__text-input {
    min-height: 52px;
    margin-left: 19px;
    margin-right: 10px;
  }

  .request-form__legend {
    font-size: 36px;
  }
  .request-form__legend::before {
    width: 100px;
    left: -109px;
  }
  .request-form__legend--comments::after {
    width: 1111px;
    left: 215px;
  }
  .request-form__legend--add::after {
    width: 1075px;
    left: 254px;
  }
  .request-form__legend--contacts::after {
    width: 777px;
    left: 549px;
  }

  .request-form__name--radio::before {
    width: 18px;
    height: 18px;
    outline-offset: 10px;
  }

  .request-form__name::before {
    margin-right: 43px;
  }

  .request-form__fieldset {
    margin: 0;
  }
  .request-form__fieldset--data {
    grid-column: 1/2;
  }
  .request-form__fieldset--type {
    grid-column: 2/3;
    padding: 20px;
    align-self: end;
    margin-left: 10px;
    max-width: 98%;
  }
  .request-form__fieldset--contacts, .request-form__fieldset--comment, .request-form__fieldset--additional {
    grid-column: 1/-1;
  }
  .request-form__fieldset--contacts {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
  }
  .request-form__fieldset--contacts .request-form__legend {
    grid-column: 1/-1;
  }
}